"use client";

import {
  Box,
  IconButton,
  Menu,
  MenuItem,
  Typography,
  Fade,
  Avatar,
} from "@mui/material";
import { useState } from "react";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import styles from "./Filter.module.css"; // ✅ Import CSS module

type Props = {
  language: "en" | "ka";
  setLanguage: (lang: "en" | "ka") => void;
  currency: "USD" | "GEL";
  setCurrency: (cur: "USD" | "GEL") => void;
};

const Filter = ({ language, setLanguage, currency, setCurrency }: Props) => {
  const [anchorElCurrency, setAnchorElCurrency] = useState<null | HTMLElement>(null);
  const [anchorElLang, setAnchorElLang] = useState<null | HTMLElement>(null);

  const openCurrency = Boolean(anchorElCurrency);
  const openLang = Boolean(anchorElLang);

  const handleCurrencyClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElCurrency(event.currentTarget);
  };

  const handleLangClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElLang(event.currentTarget);
  };

  const handleCurrencyClose = (value?: string) => {
    if (value === "USD" || value === "GEL") setCurrency(value);
    setAnchorElCurrency(null);
  };

  const handleLangClose = (lang?: "en" | "ka") => {
    if (lang) setLanguage(lang);
    setAnchorElLang(null);
  };

  return (
    <Box className={styles.container}>
      {/* Promotions */}
      <Box className={styles.promotions}>
        <Typography variant="body1" fontWeight="bold" sx={{ color: "#d81b60", p: 0 }}>
          Promotions:
        </Typography>
        <Typography variant="body1" sx={{ color: "gray", p: 1 }}>
          None available!
        </Typography>
      </Box>

      {/* Currency & Language Selector */}
      <Box className={styles.selector}>
        {/* Currency */}
        <IconButton onClick={handleCurrencyClick} className={styles.currencyBtn}>
          <Typography className={styles.currencyText}>{currency}</Typography>
          <ArrowDropDownIcon className={styles.currencyIcon} />
        </IconButton>

        <Menu
          anchorEl={anchorElCurrency}
          open={openCurrency}
          onClose={() => handleCurrencyClose()}
          TransitionComponent={Fade}
          disableScrollLock
          PaperProps={{
            sx: {
              mt: 1,
              borderRadius: 2,
              boxShadow: 3,
              minWidth: 90,
              maxHeight: 200,
              px: 1,
            },
          }}
        >
          {["USD", "GEL"]
            .filter((val) => val !== currency)
            .map((option) => (
              <MenuItem key={option} onClick={() => handleCurrencyClose(option)}>
                {option}
              </MenuItem>
            ))}
        </Menu>

        {/* Language */}
        <IconButton onClick={handleLangClick} className={styles.flagBtn}>
          <Avatar
            src={
              language === "en"
                ? "https://flagcdn.com/us.svg"
                : "https://flagcdn.com/ge.svg"
            }
            className={styles.flagAvatar}
          />
          <ArrowDropDownIcon className={styles.flagIcon} />
        </IconButton>

        <Menu
          anchorEl={anchorElLang}
          open={openLang}
          onClose={() => handleLangClose()}
          TransitionComponent={Fade}
          disableScrollLock
          PaperProps={{
            sx: {
              mt: 1,
              borderRadius: 2,
              boxShadow: 3,
              minWidth: 100,
              maxHeight: 200,
              px: 1,
            },
          }}
        >
          <MenuItem onClick={() => handleLangClose("en")}>
            <Typography>English</Typography>
          </MenuItem>
          <MenuItem onClick={() => handleLangClose("ka")}>
            <Typography>ქართული</Typography>
          </MenuItem>
        </Menu>
      </Box>
    </Box>
  );
};

export default Filter;
